<template>
  <div align="center" id="container" @click="onClick">
    <canvas width="800" height="500" id="canvas" ref="canvas">
      Oops, your browser doesn't support this application.
    </canvas>
  </div>
</template>

<script>
import img from '@/assets/fbb5.jpg'
export default {
  name: 'imgPixel',
  data () {
    return {
      canvas: null,
      context: null,
      img: null,
      imgArr: [],
      ox: 350,
      oy: 50,
      w: 100,
      h: 100,
      th: 1,
      bottom: null,
      posData: [],
      oldPos: [],
      currentData: [],
      targetData: [],
      reversed: false,
      time: null
    }
  },
  mounted () {
    for (let i = 0; i < this.h; i++) {
      this.posData[i] = []
      this.oldPos[i] = []
      this.currentData[i] = []
      for (let j = 0; j < this.w; j++) {
        this.posData[i][j] = { x: this.ox + j, y: this.oy + i }
        this.targetData = this.posData
        this.oldPos[i][j] = { x: Math.random() * 800, y: Math.random() * 600 }
        this.currentData[i][j] = { x: Math.random() * 800, y: Math.random() * 600, vx: 0, vy: 0, ax: 0.16 - Math.random() * 0.08, ay: 0.16 - Math.random() * 0.08, nx: 0.4 + Math.random() * 0.3, ny: 0.3 + Math.random() * 0.2 }
      }
    }
    this.init()
  },
  beforeDestroy () {
    clearInterval(this.time)
  },
  methods: {
    init () {
      this.canvas = document.getElementById('canvas')
      this.context = this.canvas.getContext('2d')
      this.bottom = this.canvas.height - 1
      this.img = new Image()
      this.img.src = img
      this.img.onload = () => {
        this.time = setInterval(this.run, 10)
        this.context.drawImage(this.img, 0, 0, 100, 100)
        for (let i = 0; i < this.h; i++) {
          this.imgArr[i] = []
          for (let j = 0; j < this.w; j++) {
            this.imgArr[i][j] = this.context.getImageData(j, i, 1, 1)
          }
        }
        this.context.clearRect(0, 0, this.canvas.width, this.canvas.height)
      }
    },

    run () {
      this.context.clearRect(0, 0, this.canvas.width, this.canvas.height)
      if (!this.reversed) {
        if (this.th < this.h) this.th++
        for (let i = 0; i < this.h; i++) {
          for (let j = 0; j < this.w; j++) {
            const target = this.targetData[i][j]
            const current = this.currentData[i][j]
            if (i < this.th) {
              const xDiff = target.x - current.x
              const yDiff = target.y - current.y
              if (Math.abs(xDiff) < 0.5) {
                current.x = target.x
              } else {
                current.x += (target.x - current.x) * current.ax
              }
              if (Math.abs(yDiff) < 0.5) {
                current.y = target.y
              } else {
                current.y += (target.y - current.y) * current.ay
              }
            }
            this.context.putImageData(this.imgArr[i][j], current.x, current.y)
          }
        }
      } else {
        for (let i = 0; i < this.h; i++) {
          for (let j = 0; j < this.w; j++) {
            const current = this.currentData[i][j]
            current.x += current.vx
            current.y += current.vy

            if (current.y >= this.bottom) {
              current.y = this.bottom
              current.vy = -current.ny * current.vy
              if (Math.abs(current.vy) <= 1) {
                current.vy = 0
              }
              current.vx *= current.nx
              if (Math.abs(current.vx) <= 1) current.vx = 0
            } else {
              current.vy += 1
            }
            this.context.putImageData(this.imgArr[i][j], current.x, current.y)
          }
        }
      }
    },

    onClick () {
      this.reversed = !this.reversed
      if (this.reversed) {
        for (let i = 0; i < this.h; i++) {
          for (let j = 0; j < this.w; j++) {
            const current = this.currentData[i][j]
            current.vx = (Math.random() - Math.random()) * 3
            current.vy = -Math.random() * 10
          }
        }
      } else {
        this.th = 1
        this.targetData = this.posData
      }
    }
  }
}
</script>
<style scoped>
  body {
    background:#000;
  }
</style>
